import React from 'react'
import { Menu } from 'antd'
import 'antd/dist/antd.css'

const items = [
  { label: 'Home', key: 'home' },
  { label: 'Login', key: 'login' },
  { label: 'Register', key: 'register' },
]
const navigate = ({ key }) => window.location.href = key
const currentPage = () => window.location.pathname.replace('/', '').toLowerCase()
export default Navigator = () => <Menu mode="horizontal" items={items} onClick={navigate} selectedKeys={[currentPage()]}/>
