import React from 'react'
import { Menu } from 'antd'

const items = [
  { label: 'Mathematics', key: '/category?c=Mathematics' },
  { label: 'Statistics', key: '/category?c=Statistics' },
  { label: 'Economics', key: '/category?c=Economics' },
  { label: 'Finance', key: '/category?c=Finance' },
  { label: 'Accounting', key: '/category?c=Accounting' },
  { label: 'Software Development', key: '/category?c=SoftwareDevelopment' },
  { label: 'Marketing', key: '/category?c=Marketing' }
]

const navigate = ({ key }) => window.location.href = key
const currentPage = () => {
  let output = ''
  items.forEach((item) => {
    if (window.location.pathname.indexOf(item.label)!==-1) {
      output = item.label
      alert(output)
    }
  })
  return output
}
export default Navigator = () => <Menu mode='horizontal' align='center' items={items} onClick={navigate} selectedKeys={[currentPage]}/>
